import React, { useEffect, useState } from 'react'
import faker from "faker";
import Gallery from './Gallery';

function generateCardsData(count) {
    const cards = [];
    for (let i = 0; i < count; i++) {
      const card = {
        id: i + 1,
        name: faker.name.findName(),
        first_name: faker.name.firstName(),
        last_name: faker.name.lastName(),
        current_title: faker.name.jobTitle(),
        location: faker.address.city() + ", " + faker.address.stateAbbr(),
        phone_number: faker.phone.phoneNumberFormat(),
        personal_email: faker.internet.email(),
        linkedIn: faker.internet.url(),
        experience: [
          faker.name.jobTitle() + " - " + faker.company.companyName(),
          faker.name.jobTitle() + " - " + faker.company.companyName()
        ],
        education: [
          faker.random.words() + " - " + faker.company.companyName(),
          faker.random.words() + " - " + faker.company.companyName()
        ]
      };
      cards.push(card);
    }
    return cards;
  }
  
const FakerData = () => {
    const [cardsData, setCardsData] = useState([]);

    useEffect(() => {
      const generatedData = generateCardsData(10);
      setCardsData(generatedData);
    }, []);
  return <Gallery cardsData={cardsData} />;
}

export default FakerData