import React, { useState } from "react";
import JsonServer from "./JsonServer";
import FakerData from "./FakerData";

const Tab = () => {
  const [activeTab, setActiveTab] = useState("JSON");

  const handleTabChange = (tab) => {
    setActiveTab(tab);
  };
  return (
    <>
      <div className="tabs">
        <div className="tab" onClick={() => handleTabChange("JSON")}>
          <span className={activeTab === "JSON" ? "active" : ""}>JSON</span>
        </div>
        <div className="tab" onClick={() => handleTabChange("Faker")}>
          <span className={activeTab === "Faker" ? "active" : ""}>Faker</span>
        </div>
      </div>
      {activeTab === "JSON" && <JsonServer />}
      {activeTab === "Faker" && <FakerData />}
    </>
  );
};

export default Tab;
